<i18n>
{
  "en": {
    "Store": "Store",
    "Nearest": "Nearest",
    "WalMart": "WalMart",
    "address": "Zhongshan avenue tangxia",
    "Charger": "Charger",
    "Borrow": "Borrow {count}",
    "Repay": "Repay {count}",
    "unit": "{count} km"
  },
  "zh": {
    "Store": "门店分类",
    "Nearest": "离我最近",
    "WalMart": "沃尔玛",
    "address": "中山大道棠下",
    "Charger": "提供座充",
    "Borrow": "可借{count}台",
    "Repay": "可还{count}台",
    "unit": "{count} 千米"
  }
}
</i18n>
<template>
  <div class="store">
    <head-top :title='$t("Store")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <store-category></store-category>
      <div class="store-list">
        <ul>
          <li @click="storeDetails">
            <div class="label">
              <span>{{ $t("Nearest") }}</span>
            </div>
            <div class="list-left">
              <span></span>
            </div>
            <div class="list-right">
              <span>
                <strong>{{ $t("WalMart") }}</strong>
                <b>{{ $t("unit",{count: '1.00'}) }}</b>
              </span>
              <span>
                <img src="../../assets/store_images/time.png" alt="">
                <strong>10.00-23.00</strong>
              </span>
              <span>
                <img src="../../assets/store_images/add.png" alt="">
                <strong>{{ $t("address") }}</strong>
              </span>
              <span>
                <div class="charger">
                  <img src="../../assets/store_images/charger-default.png" alt="">
                  <strong>{{ $t("Charger") }}</strong>
                </div>
                <div class="power">
                  <div class="interval"></div>
                  <img src="../../assets/store_images/baby.png" alt="">
                  <strong>{{ $t("Borrow",{count: 3}) }} {{ $t("Repay",{count: 3}) }}</strong>
                </div>
              </span>
            </div>
          </li>
          <li @click="storeDetails">
            <div class="label">
              <span>{{ $t("Nearest") }}</span>
            </div>
            <div class="list-left">
              <span></span>
            </div>
            <div class="list-right">
              <span>
                <strong>{{ $t("WalMart") }}</strong>
                <b>{{ $t("unit",{count: '1.00'}) }}</b>
              </span>
              <span>
                <img src="../../assets/store_images/time.png" alt="">
                <strong>10.00-23.00</strong>
              </span>
              <span>
                <img src="../../assets/store_images/add.png" alt="">
                <strong>{{ $t("address") }}</strong>
              </span>
              <span>
                <div class="charger">
                  <img src="../../assets/store_images/charger-default.png" alt="">
                  <strong>{{ $t("Charger") }}</strong>
                </div>
                <div class="power">
                  <div class="interval"></div>
                  <img src="../../assets/store_images/baby.png" alt="">
                  <strong>{{ $t("Borrow",{count: 3}) }} {{ $t("Repay",{count: 3}) }}</strong>
                </div>
              </span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import storeCategory from '../../components/StoreCategory/index'
export default {
  name: 'store',
  data () {
    return {}
  },

  components: {
    headTop,
    storeCategory
  },

  methods: {
    goBackHander () {
      // console.log(123)
    },

    storeDetails () {
      this.$router.push({path: '/details'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.store{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .store-list{
      .wh(100%, calc(~"100% - 80px"));
      position: absolute;
      left: 0;
      top: 80px;
      overflow: auto;
      ul{
        width: 100%;
        li{
          .wh(720px, 320px);
          background: url('../../assets/store_images/bg.png') 0 0 no-repeat;
          background-size: cover;
          margin: 0 auto 20px auto;
          position: relative;
          .label{
            .wh(120px, 55px);
            position: absolute;
            left: 6%;
            top: -2px;
            font-size: 26px;
            color: #405C72;
            background: url('../../assets/store_images/label.png') 0 0 no-repeat;
            background-size: cover;
            span{
              width: 110%;
              display: block;
              .zoom-font(0.8);
              position: absolute;
              left: 16px;
              top: 22%;
            }
          }
          .list-left{
            .wh-f(32%, 100%);
            position: relative;
            span{
              .wh(152px, 152px);
              .f-center;
              background: #CCCCCC;
              border-radius: 20px;
            }
          }
          .list-right{
            .wh-f(68%, 100%);
            font-size: 26px;
            text-align: left;
            padding: 0 50px 0 5px;
            span:nth-child(1) {
              .wh-l(100%, 110px);
              display: block;
              strong{
                font-size: 30px;
                font-weight: bold !important;
              }
              b{
                float: right;
                color: #A9A9A9;
              }
            }
            span:nth-child(2) {
              .wh-l(100%, 52px);
              display: block;
              color: #A9A9A9;
              font-size: 30px;
              img{
                height: 30px;
                margin-right: 12px;
                position: relative;
                top: 5px;
              }
            }
            span:nth-child(3) {
              .wh-l(100%, 52px);
              display: block;
              color: #A9A9A9;
              font-size: 26px;
              img{
                height: 30px;
                margin-right: 24px;
                position: relative;
                top: 8px;
                left: 4px;
              }
            }
            span:nth-child(4) {
              .wh-l(110%, 100px);
              display: block;
              color: #626262;
              .charger{
                .wh-f(36%, 100%);
                img{
                  height: 38px;
                  margin-right: 12px;
                  position: relative;
                  top: 10px;
                  left: 4px;
                }
              }
              .power{
                .wh-f(64%, 100%);
                position: relative;
                .interval{
                  .wh-f(2px, 30px);
                  background: #EEEEEE;
                  margin: 35px 16px 0 6px;
                }
                img{
                  height: 38px;
                  margin-right: 12px;
                  position: relative;
                  top: 10px;
                  left: 4px;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



