<i18n>
{
  "en": {
    "Devicetype": "Devicetype",
    "Area": "Area",
    "All": "All",
    "Charger": "Charger",
    "Battery": "Battery",
    "TianheDistrict": "Tianhe District",
    "HuangpuDistrict": "Huangpu District"
  },
  "zh": {
    "Devicetype": "设备类型",
    "Area": "区域",
    "All": "全部",
    "Charger": "座充",
    "Battery": "充电宝",
    "TianheDistrict": "天河区",
    "HuangpuDistrict": "黄埔区"
  }
}
</i18n>

<template>
  <div class="store-category">
    <div class="mask" v-if="deviceShow||areaShow"></div>
    <div class="nav">
      <div class="device" @click="selectNav(1)">
        <span :class="{'nav-bg': deviceShow}">{{ $t('Devicetype') }}</span>
        <img v-if="!deviceShow" src="../../assets/store_images/down.png" alt="">
        <img v-if="deviceShow" src="../../assets/store_images/up.png" alt="">
      </div>
      <div class="area" @click="selectNav(2)">
        <span :class="{'nav-bg': areaShow}">{{ $t('Area') }}</span>
        <img  v-if="!areaShow" src="../../assets/store_images/down.png" alt="">
        <img  v-if="areaShow" src="../../assets/store_images/up.png" alt="">
      </div>
    </div>
    <ul class="device-list" v-show="deviceShow">
      <li @click="deviceHander(0)" class="bomPixel">
        <span>{{ $t('All') }}</span>
        <img v-if="deviceActive===0" src="../../assets/store_images/click.png" alt="">
      </li>
      <li @click="deviceHander(1)" class="bomPixel">
        <span>{{ $t('Charger') }}</span>
        <img v-if="deviceActive===1" src="../../assets/store_images/click.png" alt="">
      </li>
      <li @click="deviceHander(2)" class="bomPixel">
        <span>{{ $t('Battery') }}</span>
        <img v-if="deviceActive===2" src="../../assets/store_images/click.png" alt="">
      </li>
    </ul>
    <div class="area-list" v-if="areaShow">
      <ul class="list-left">
        <li @click="areaHander(-1)"
            :class="{'area-bg': areaActive===-1}">
          <span v-show="areaData.length!==0">{{ $t('All') }}</span>
          <img v-if="areaActive===-1"
               src="../../assets/store_images/click.png" alt="">
        </li>
        <li v-for="(item, index) in areaData"
            v-bind:key="index"
            @click="areaHander(index)"
            :class="{'area-bg': areaActive===index}">
          <span v-show="areaData.length!==0">{{item}}</span>
          <img v-if="areaActive===index"
               src="../../assets/store_images/click.png" alt="">
        </li>
      </ul>
      <ul class="list-right"></ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'store-category',
  data () {
    return {
      deviceShow: false,
      deviceActive: 0,
      areaData: [
        this.$t('TianheDistrict'),
        this.$t('HuangpuDistrict')
      ],
      areaShow: false,
      areaActive: -1 // 区域选择
    }
  },

  methods: {
    selectNav (num) {
      if (num === 1) {
        this.deviceShow = !this.deviceShow
        this.areaShow = false
      } else {
        this.areaShow = !this.areaShow
        this.deviceShow = false
      }
    },

    // 设备类型
    deviceHander (num) {
      this.deviceActive = num
      this.deviceShow = false
    },

    // 区域
    areaHander (num) {
      this.areaActive = num
      this.areaShow = false
    }
  },
  watch: {
    '$i18n.locale' (val, oldVal) {
      this.$set(this.areaData, 0, this.$t('TianheDistrict'))
      this.$set(this.areaData, 1, this.$t('HuangpuDistrict'))
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.store-category{
  .wh(100%, 100%);
  position: absolute;
  left: 0;
  top: 0;
  .mask{
    .wh(100%, calc(~"100% - 4px"));
    position: absolute;
    left: 0;
    top: 4px;
    background: rgba(0, 0, 0, 0.5);
    z-index: 10;
  }
  .nav{
    .wh-l(100%, 80px);
    font-size: 30px;
    color: #AAAAAA;
    background: #FFF;
    position: relative;
    z-index: 10;
    .nav-bg{
      color: @themeColor;
    }
    .device, .area{
      .wh-f(50%, 100%);
      img{
        height: 10px;
        margin-left: 10px;
        position: relative;
        top: -5px;
      }
    }
    .device{
      width: 36%;
    }
    .area{
      width: 64%;
      text-align: left;
      padding-left: 8%;
    }
  }
  .device-list{
    .wh(100%, 270px);
    position: absolute;
    left: 0;
    top: 80px;
    z-index: 10;
    background: #FFF;
    font-size: 30px;
    color: #AAAAAA;
    li{
      .wh-l(100%, 90px);
      position: relative;
      text-align: left;
      padding-left: 8%;
      color: #585858;
      img{
        height: 34px;
        position: absolute;
        top: 50%;
        margin-top: -17px;
        right: 26px;
      }
    }
  }
  .area-list{
    .wh(100%, calc(~"100% - 80px"));
    background: #FFF;
    font-size: 30px;
    color: #AAAAAA;
    position: absolute;
    left: 0;
    top: 80px;
    z-index: 10;
    text-align: left;
    .list-left{
      .wh-f(36%, 100%);
      overflow: auto;
      background: #EEEEEE;
      .area-bg{
        background: #FFF;
      }
      li{
        .wh-l(100%, 90px);
        position: relative;
        color: #585858;
        padding-left: 18%;
        .text-over;
        img{
          height: 34px;
          position: absolute;
          top: 50%;
          margin-top: -17px;
          right: 26px;
        }
      }
    }
    .list-right{
      .wh-f(64%, 100%);
      overflow: auto;
    }
  }
}
</style>



